.event-details-page {
  padding: 20px;
  background-color: #141414;
  color: #ffffff;
  font-family: Arial, sans-serif;
}

.breadcrumb {
  margin-bottom: 20px;
  font-size: 14px;
  color: #cccccc;
}

.breadcrumb a {
  color: #00BFFF;
  text-decoration: none;
}

.breadcrumb a:hover {
  text-decoration: underline;
}

/* Main Title */
.event-details-page h1 {
  font-size: 28px;
  color: #00BFFF;
  margin-bottom: 20px;
}

/* Event Information Section */
.event-info p {
  font-size: 16px;
  margin: 5px 0;
}

.event-info p strong {
  color: #00BFFF;
}

.event-info button {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  color: white;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.event-info button:hover {
  background-color: #0056b3;
}

/* Group and Knockout Stages Sections */
.group-stages-section,
.knockout-stages-section {
  margin-top: 30px;
}

.group-stages-section h2,
.knockout-stages-section h2 {
  font-size: 22px;
  color: #00BFFF;
  margin-bottom: 10px;
}

/* No Stages Message */
.group-stages-section p,
.knockout-stages-section p {
  color: #cccccc;
  font-size: 14px;
  margin: 10px 0;
}

/* Buttons for generating stages */
.group-stages-section button,
.knockout-stages-section button {
  padding: 8px 12px;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.group-stages-section button:hover,
.knockout-stages-section button:hover {
  background-color: #218838;
}

/* List of stages */
.group-stages-section ul,
.knockout-stages-section ul {
  list-style-type: none;
  padding: 0;
}

.group-stages-section li,
.knockout-stages-section li {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
  background-color: #1f1f1f;
  border-radius: 4px;
  margin-bottom: 8px;
}

.group-stages-section li button,
.knockout-stages-section li button {
  padding: 6px 10px;
  background-color: #00BFFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.group-stages-section li button:hover,
.knockout-stages-section li button:hover {
  background-color: #007acc;
}

.error-container {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  background-color: #f8d7da;
  color: #721c24;
  padding: 10px 20px;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  max-width: 600px;
  width: 80%;
  text-align: center;
}

.error-icon {
  fill: #721c24;
  width: 24px;
  height: 24px;
  margin-right: 10px;
}

.error-message {
  font-weight: bold;
}
